@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.section {
  /* background-color: #02d46b; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.a-link {
  font-size: large;
  color: #1687A7;
  margin: 10px;
  text-decoration: none;
  border-radius: 12px;
  padding: 8px 15px 8px 15px;
  border: 4px solid transparent;
  font-weight: bold;
}

.bright {
  color: #FFC947;
  font-weight: bold;
}
.bar {
    background: #ffffffa6;
    color: #1687A7;
    min-height: 29px;
    padding-top: 23px;
    padding-bottom: 15px;
    position: fixed;
    width: 100%;
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
    box-shadow: 0px 0px 10px -7px black;
}

.burgerBox {
    text-align: left;
    padding-left: 25px;
    font-size: 25px;
}

.burgerBox>svg {
    cursor: pointer;
}
nav {
    text-align: right;
}

nav>a:hover {
    border: 4px solid;
    transition: 1s;
}
ul {
    list-style: none;
    text-align: center;
    padding: 0px;
}

li {
    padding: 10px;
    color: #1687A7;
    text-transform: uppercase;
    cursor: pointer;
    transition: 1s;
}

li:hover {
    background: #D3E0EA;
    transition: 0.5s;
}

.boxDrawer {
    width: 250px;
}
.content {
    display: flex;
    flex-direction: column;
    max-width: 750px;
    margin: 20px;
    margin-top: 100px;
    font-size: smaller;
}
.project-link {
    background: #1687a7;
    text-align: center;
    border-radius: 14px;
    box-shadow: 0px 0px 15px -6px black;
    transition: 1s;

    text-decoration: none;
    color: white;
}

.project-link:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.2s;
    cursor: pointer;
}

img {
    border-radius: 14px 14px 0px 0px;
}
.img-link {
    margin-right: 40px;
    margin-left: 40px;
    color: white
}

.linkedin:hover {
    color: #134466;
}

.github:hover {
    color: #434343;
}


.mail:hover {
    color: #ce2751;
}

.container {
    padding-top: 20px;
    font-size: 3em;
}
footer {
    background-color: #2a2a2a;
    color: #fefcfb;
    display: flex;
    justify-content: center;
    padding: 10px;
}

a {
    text-decoration: none;
    color: orange;
    font-weight: bold;
}
